<template>
	<view class="xinyi-content">
		<view class="login-form__view">
			<view class="login-top__view">
				<image src="/static/image/logo.svg" class="logo__image"></image>
				<view class="login-title__view">兵棋客户端</view>
			</view>
			<view class="login-input__view">
				<uni-easyinput
					v-model="user.loginName"
					placeholder="请输入用户名"
					trim
					primaryColor="#215e21"
				></uni-easyinput>
				<uni-easyinput
					v-model="user.password"
					placeholder="请输入密码"
					type="password"
					trim
					primaryColor="#215e21"
				></uni-easyinput>
			</view>
			<view class="login-btn__view">
				<button class="login__btn" hover-class="login-hover" @click="login">登录</button>
				<button class="register__btn" @click="toReg()">注册</button>
			</view>
		</view>
	</view>
</template>

<script>
import { login } from '@/api/user.js';
import { initWebSocket, closeSocketTask } from '@/api/websocket.js';
export default {
	data() {
		return {
			user: {
				userName: '',
				password: ''
			}
		};
	},
	onLoad() {
		let user = uni.getStorageSync('user');
		if (user) {
			uni.setStorageSync('user', null);
			closeSocketTask();
		}
	},
	methods: {
		login() {
			if (!this.user.loginName || !this.user.password) {
				return new uni.showToast({
					title: '请输入用户名、密码',
					icon: 'none'
				});
			}
			uni.showLoading({
				title: '登录中...',
				mask: true
			});
			login(this.user)
				.then((res) => {
					uni.hideLoading();
					// console.log(res);
					if (res.data.code == 200) {
						let user = res.data.data.user;
						let token = res.data.data.token;
						uni.setStorageSync('token', token);
						uni.setStorageSync('user', user);
						initWebSocket();
						uni.reLaunch({
							url: '/pages/mode/select'
						});
					}
				})
				.catch((err) => {
					uni.hideLoading();
				});
		},
		toReg() {
			uni.navigateTo({
				url: '/pages/login/register'
			});
		}
	}
};
</script>

